.messageInstance{
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 1rem;
}

.messageText {
    opacity: 50%;
    margin-left: 1rem;
    position:relative;
    background-color: rgb(0,36,13);
    color: antiquewhite;
    min-width: 100px;
    max-width: border-box;
    max-width: 400px;
    border-radius: 10px 10px 10px 0;
}
.messageText:before {
    content: "";
    position:absolute;
    bottom: 0;
    left:-40px;
    height:20px;
    width: 40px;
    border-bottom-right-radius: 50%;
    box-shadow: 20px 0 0 0 rgb(0,36,13);
}


.userName {
    color: aliceblue;
    display: flex;
    align-items: flex-start;
    padding-top: 0.2rem;
    padding-left: 0.4rem;
}
.userMessage {
    display: flex;
    align-items: center;
    padding: 5px 10px 5px 20px;
}
.userMessageTime {
    font-size: 10px;
    color: chartreuse;
    text-align: end;
    padding-bottom: 0.2rem;
    padding-right: 0.4rem;
}

.img {
    height: 50px;
    align-items: flex-end;
    border-radius: 50%;
}